const OpenModal = () => {
  modal.classList.remove('modal-hidden');
  modal.classList.add('modal-show');
};


openModal.forEach(element => element.addEventListener('click', OpenModal));

$('.countdown').downCount({
  date: '05/13/2019 12:00:00',
  offset: +10
});


const CloseModal = () => {
  modal.classList.remove('modal-show');
  modal.classList.add('modal-hidden');
};


$('#form').on('submit', function(e){
  e.preventDefault();
  var fd = new FormData( this );
  $.ajax({
    url: 'send.php',
    type: 'POST',
    contentType: false, 
    processData: false, 
    data: fd,
    success: function(msg){
      if(msg === 'ok') {
        alert('Отправлено');
      } else {
        alert('Ошибка');
      }
    }
  });
});

