const slideShow = () => {
  secondSlide.style.opacity = 0;
  firstSlide.style.opacity = 0;

  allSiledes.forEach(slide => {
    if(slide.classList.contains('active-slide')) {
      slide.classList.remove('active-slide');
    } else {
      slide.classList.add('active-slide');
      animateItem(slide);
    }
  });

  // console.log(header.style.background );

  // if(header.style.background === 'linear-gradient(to right, rgb(240, 152, 25), rgb(237, 222, 93))') {
  //   header.style.background = 'linear-gradient(to right, #8e2de2, #4a00e0)';
  // } else {
  //   header.style.background = 'linear-gradient(to right, rgb(240, 152, 25), rgb(237, 222, 93))';
  // }

  setTimeout(slideShow, 5000);
};

slideShow();

closeModal.addEventListener('click', CloseModal);


acc.forEach(el => {
  el.addEventListener('click', e=>{
    let sibling = e.target.nextElementSibling;  
    e.target.classList.toggle("active");
    if(sibling.style.maxHeight) {
      sibling.style.maxHeight = null;
    } else {
      sibling.style.maxHeight = sibling.scrollHeight + "px";
    }
  });
});
